<template>
  <div
    class="text_left search_box"
    v-loading="isSeaching"
    element-loading-text="Loading..."
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <el-input
      v-model="owner"
      placeholder="owner address"
      clearable
      prefix-icon="el-icon-search"
      :autosize="{ minRows: 1, maxRows: 2 }"
      style="width:370px"
      @change="search"
    />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ElMessage } from "element-plus";
export default {
  data: function() {
    return {
      owner: "",
      isSeaching: false,
    };
  },
  computed: { ...mapGetters(["isHadHero"]) },
  watch: {
    isHadHero: function(val, val_lod) {
      console.log("isHadHero val=", val, val_lod);
      if (!val) {
        // message: owner no hero
        ElMessage.warning(`${this.owner} no hero`);
        this.owner = "";
      }
    },
  },
  methods: {
    search: async function() {
      console.log("search  owner=", this.owner);
      if (!this.owner || this.owner.trim() == "") {
        return;
      }
      if (this.isSeaching) return;
      this.isSeaching = true;
      await this.$store.dispatch("hero/search", { owner: this.owner });
      this.isSeaching = false;
      this.owner = "";
      ElMessage.success("success");
    },
  },
};
</script>

<style scoped>
.text_left {
  text-align: left;
}
.search_box {
  padding-top: 30px;
  padding-left: 30px;
}
</style>
