<template>
  <div class="head">
    <div class="links">
      <div id="nav">
        <router-link to="/">PiBNX</router-link> |
        <router-link to="/about">About</router-link>
      </div>
    </div>
    <div class="btns">
      <el-button v-if="isShowInstallMetaMaskBtn" type="primary"
        >install metamask</el-button
      >
      <el-button v-if="isShowConnectMetaMaskBtn" type="primary"
        >connet</el-button
      >
      <el-button v-if="isShowAddressOfEth" type="text">{{
        addressEth
      }}</el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "nav",
  data: function() {
    return {
      isShowInstallMetaMaskBtn: false,
      isShowConnectMetaMaskBtn: false,
      isShowAddressOfEth: true,
    };
  },
  computed: {
    ...mapGetters([
      "isProviderInjected",
      "isEthereumEnabled",
      "isEthAddressGeted",
      "addressEth",
    ]),
  },
  watch: {
    isProviderInjected: function(val) {
      this.isShowInstallMetaMaskBtn = !val;
      // if (val == true) {
      //   this.isShowInstallMetaMaskBtn = false;
      // } else {
      //   this.isShowInstallMetaMaskBtn = true;
      // }
    },
    isEthereumEnabled: function(val) {
      if (this.isShowInstallMetaMaskBtn) {
        this.isShowConnectMetaMaskBtn = false;
        return;
      }
      this.isShowConnectMetaMaskBtn = !val;
    },
    isEthAddressGeted: function(val, val_old) {
      console.log("watch val=", val, val_old);
      if (this.isShowInstallMetaMaskBtn || this.isShowConnectMetaMaskBtn) {
        this.isShowAddressOfEth = false;
        return;
      }
      this.isShowAddressOfEth = val;
    },
  },
};
</script>

