<template>
  <div>
    <el-table :data="heros" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="40" />
      <el-table-column label="序号" width="50">
        <template #default="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="英雄ID" width="550">
        <template #default="scope">
          {{ scope.row["tokenId"] }}
        </template>
      </el-table-column>
      <el-table-column
        label="类型"
        :filters="[
          { text: '战士', value: '战士' },
          { text: '盗贼', value: '盗贼' },
          { text: '法师', value: '法师' },
          { text: '游侠', value: '游侠' },
        ]"
        :filter-method="filterHandler"
      >
        <template #default="scope">
          {{ scope.row["roleType"] }}
        </template>
      </el-table-column>
      <el-table-column label="力量">
        <template #default="scope">
          {{ scope.row[0][0] }}
        </template>
      </el-table-column>
      <el-table-column label="敏捷">
        <template #default="scope">
          {{ scope.row[0][1] }}
        </template>
      </el-table-column>
      <el-table-column label="体质">
        <template #default="scope">
          {{ scope.row[0][2] }}
        </template>
      </el-table-column>
      <el-table-column label="意志">
        <template #default="scope">
          {{ scope.row[0][3] }}
        </template>
      </el-table-column>
      <el-table-column label="智力">
        <template #default="scope">
          {{ scope.row[0][4] }}
        </template>
      </el-table-column>
      <el-table-column label="精神">
        <template #default="scope">
          {{ scope.row[0][5] }}
        </template>
      </el-table-column>
      <el-table-column label="等级">
        <template #default="scope">
          {{ scope.row[0][6] }}
        </template>
      </el-table-column>
      <el-table-column label="总属性">
        <template #default="scope">
          {{
            parseInt(scope.row[0][0]) +
              parseInt(scope.row[0][1]) +
              parseInt(scope.row[0][2]) +
              parseInt(scope.row[0][3]) +
              parseInt(scope.row[0][4]) +
              parseInt(scope.row[0][5]) +
              parseInt(scope.row[0][6])
          }}
        </template>
      </el-table-column>
      <el-table-column label="是否升级高级工作">
        <template #default="scope">
          {{
            scope.row["roleType"] == "战士" &&
            parseInt(scope.row[0][0]) > 85 &&
            parseInt(scope.row[0][2]) > 60 &&
            parseInt(scope.row[0][6]) > 1
              ? "是"
              : "否"
          }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "hero list",
  data: function() {
    return {
      multipleSelection: [],
    };
  },
  computed: {
    ...mapGetters(["heros"]),
  },
  watch: {
    heros: function(val, val_old) {
      console.log("watch heros", val, val_old);
    },
  },
  methods: {
    handleSelectionChange: function(val) {
      this.multipleSelection = val;
      console.log("handleSelectionChange rows=", this.multipleSelection);
    },
    filterHandler: function(val, row, col) {
      // console.log(`var=${val}`, val, `row=${row}`, row, `col=${col}`, col);
      if (col["label"] == "类型") {
        return val == row["roleType"];
      }
      return false;
    },
  },
};
</script>

<style></style>
