import Web3 from "web3";

const state = {
  // pipe login with metamask wallet
  web3: null,
  address_eth: "0x",
  // pipe cantracts on bsc blockchain

  // pipe feel : switch pipe canditions
  connect_wallet_status: 0, // enum: 0-initAndAutoConnect,1-needInstallMetamask,2-needMannulConnect,3-netError,4-chainNetError,5-ok
  is_contracts_ated: false,
  is_provider_injected: false,
  is_ethereum_enabled: false,
  is_eth_address_geted: false,
};
const mutations = {
  SET_CONNECT_WALLET_STATUS: (state, s) => {
    state.connect_wallet_status = s;
  },
  SET_IS_CONTRACTS_ATED: (state, s) => {
    state.is_contracts_ated = s;
  },
  SET_IS_PROVIDER_INJECTED: (state, s) => {
    state.is_provider_injected = s;
  },
  SET_WEB3: (state, web3) => {
    state.web3 = web3;
  },
  SET_IS_ETHEREUM_ENABLED: (state, enable) => {
    state.is_ethereum_enabled = enable;
  },
  SET_IS_ETH_ADDRESS_GETED: (state, g) => {
    console.log("set is eth address geted ", g);
    state.is_eth_address_geted = g;
  },
  SET_ADDRESS_ETH: (state, addr) => {
    state.address_eth = addr;
  },
};
const actions = {
  async init({ state, dispatch }) {
    console.log("web3s init : web3s.state=", state);
    dispatch("provideCheck");
  },
  async provideCheck({ commit, dispatch }) {
    if (window.ethereum) {
      commit("SET_IS_PROVIDER_INJECTED", true);
      // action-1: constrct web3 instance with window.ethereum as provider
      dispatch("web3InstanceNew");
    } else {
      //env-case-1: no install metamask
      commit("SET_IS_PROVIDER_INJECTED", false);
      // action-2: tip to user that need install metamask, and show a install button
    }
  },
  async web3InstanceNew({ commit, dispatch }) {
    const web3 = new Web3(window.ethereum);
    commit("SET_WEB3", web3);
    // action-3: subscribe events: connect/ chain change/ account change
    // action-4: auto connect
    dispatch("connectMetamask");
  },
  async connectMetamask({ commit, dispatch }) {
    await window.ethereum.enable().catch((err) => {
      // user-action-1: refuse/cancel to connect wallet to this dapp
      console.log(err);
      // ignore or tip,then user can click connect btn to retry connect wallet
      commit("SET_IS_ETHEREUM_ENABLED", false);
    });
    commit("SET_IS_ETHEREUM_ENABLED", true);
    dispatch("getEthAddress");
  },
  async getEthAddress({ state, commit, dispatch }) {
    const address_eth = await state.web3.eth.getCoinbase();
    console.log("address_eth:", address_eth);

    commit("SET_IS_ETH_ADDRESS_GETED", true);
    commit("SET_ADDRESS_ETH", address_eth);
    let owner = address_eth;
    // TODO: for test,need remove
    owner = "0x56c9b5997c348139d12218da8a2aa4942b08575d";
    await dispatch("hero/search", { owner }, { root: true });
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
